"use client"
 
import Link from "next/link"
import ThemeSwitcher from "@/components/theme-switcher"
import ExampleComponent from "@/components/ExampleComponent"

import styles from "./page.module.css"
 
export default function Home() {
  return (
    <div className="container mx-auto px-32 py-32 bg-background text-foreground min-h-screen">
      {/* Increased vertical spacing between header and content */}
      <div className="flex justify-between items-center mb-32">
        <h1 className="text-3xl font-bold text-primary-500">School Management System</h1>
        <ThemeSwitcher />
      </div>
 
      {/* Removed duplicate heading for better layout */}
 
      {/* Increased gap between cards and added bottom margin */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-32 m-32 mb-64">
        
        <Link href="/kalender" className="block h-full">
          <div className="bg-content1 shadow-md rounded-lg p-32 hover:shadow-lg transition-shadow h-full">
            <h2 className="text-xl font-semibold text-content1-foreground mb-32">Kalender</h2>
            <p className="text-default-700">Verwalte deine Events und Tests</p>
          </div>
        </Link>

        <Link href="/noten" className="block h-full">
          <div className="bg-content1 shadow-md rounded-lg p-32 hover:shadow-lg transition-shadow h-full">
            <h2 className="text-xl font-semibold text-content1-foreground mb-32">Noten</h2>
            <p className="text-default-700">Trage deine Noten ein und berechne deinen Schnitt</p>
          </div>
        </Link>

        <Link href="/module" className="block h-full">
          <div className="bg-content1 shadow-md rounded-lg p-32 hover:shadow-lg transition-shadow h-full">
            <h2 className="text-xl font-semibold text-content1-foreground mb-32">Module</h2>
            <p className="text-default-700">Trage deine Noten ein und berechne deinen Schnitt</p>
          </div>
        </Link>

      </div>
 
      {/* Added margin above the example component */}
      <div className="mt-64">
        <ExampleComponent />
      </div>
    </div>
  )
}
